@import url(https://fonts.googleapis.com/css2?family=Antic+Slab&display=swap);
.body {
  background-color: #ffffff;
  margin: 0px;
}

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  font-family: "Antic Slab", Arial, Helvetica, sans-serif;
}

h1 {
  font-size: 40px;
  color: #857785;
  text-shadow: #f8b692 0px 3px 3px;
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;
}

button {
  font-size: 24px;
  height: 35px;
  width: 300px;
  background-color: #9b8da0;
  color: #ffffff;
  font-family: "Antic Slab", Arial, Helvetica, sans-serif;
  box-shadow: #f8b692 0px 3px 3px;
}

button:hover {
  background-color: #a888c4;
  font-size: 26px;
  height: 40px;
  width: 315px;
  box-shadow: #ffd5b9 2px 6px 5px;
}

.imgcont {
  width: 100%;
  margin: 0;
  display: flex;
  flex-direction: row;
  border-radius: 10px;
}

.photos {
  background-color: #ffffff;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  margin-top: 1%;
}

img {
  opacity: 0.75;
  width: 250px;
  height: 250px;
  object-fit: cover;
  overflow: hidden;
  margin: 3% 2% 2% 2%;
  padding: 1% 2% 1% 2%;
  border-radius: 10px;
  box-shadow: #f8b692 0px 3px 3px;
  background-color: #ffffff;
}

img:hover {
  opacity: 1;
  width: 300px;
  height: 300px;
  border-radius: 20px;
  box-shadow: #ffd5b9 2px 6px 5px;
}

